.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(231, 171, 171, 0.4);
  &.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
}

.modal-purchase {
  display: flex;
  flex-direction: column;
  position: absolute;
  min-width: 400px;
  min-height: 500px;
  top: 50%;
  left: 50%;
  padding: 40px;
  background-color: $modal-bg-color;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &-close {
    position: absolute;
    cursor: pointer;
    top: 8px;
    right: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin-left: auto;
    width: 30px;
    height: 30px;
    border: 2px solid $primary-title-color;
    border-radius: 50%;
    background-color: transparent;

    &:hover {
      fill: $accent-color;
    }
  }

  &-form {
    margin-top: 30px;
  }
  &-title {
    font-family: $title-fonte-family;
    width: 370px;
    height: 60px;
    margin-bottom: 0;
    font-weight: bold;
    font-size: 25px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.03em;
    color: $primary-title-color;
  }
  &-input {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 10px;

    &:focus {
      border: 1px solid $accent-color;
    }
  }

  &-input .modal-purchase-input-label {
    height: 40px;
    padding-left: 40px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    box-sizing: border-box;
    border-radius: 4px;
    outline: none;
  }

  &__comment-box {
    padding: 12px 16px;
    margin-bottom: 0px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    outline: none;
    border-radius: 4px;
    background-color: transparent;
    color: $secondary-text-color;
  }

  &-lable-txt {
    width: 50px;
    height: 14px;
    font-size: 12px;
    line-height: 1.167;
    margin-bottom: 4px;
  }
  &-submit {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 200px;
    height: 50px;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.87;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    letter-spacing: 0.06em;
    color: #ffff;
    background-color: $primary-title-color;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 4px;
    border: none;
  }
}

// icon Rules

.modal-purchase-input-icon {
  position: absolute;
  top: 29px;
  left: 12px;

  .modal-purchase-input:focus-within & {
    fill: $accent-color;
  }
}

.checkbox {
  position: relative;
  margin-top: 20px;

  &-text {
    margin-left: 30px;
    font-size: 12px;
    line-height: 1.166;
    color: #000000;
  }
  &-link {
    display: inline-flex;
    color: $accent-color;
  }
  &-icon {
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15px;
    height: 15px;
    border: 2px solid black;
    border-radius: 2px;
  }

  &-icon-svg {
    width: 15px;
    height: 15px;
    fill: $modal-bg-color;
  }

  &-old:focus:checked ~ &-icon {
    background-color: $accent-color;
    border: none;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
  &-old:focus ~ &-icon {
    border-color: $accent-color;
  }

  &__old :checked ~ &__text {
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
}
